.section__box {
  padding: 0 10px;
}

.line {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 0 auto 60px;
  background-color: rgba(255, 255, 255, 0.5);
}

.blog_box {
  width: 100%;
  max-width: 1296px;

  margin: 60px auto 0;
}

.line_bottom {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 120px auto 30px;
  background-color: rgba(255, 255, 255, 0.5);
}

@media (max-width: 1024px) {
  .line_bottom {
    margin: 60px auto 30px;
  }
}

@media (max-width: 730px) {
  .blog_box {
    margin: 40px auto 0;
  }

  .line {
    margin: 0 auto 25px;
  }
}

@media (max-width: 570px) {
  .blog_box {
    margin: 25px auto 0;
  }

  .line {
    margin: 0 auto 10px;
  }
}
