.section__box {
  padding: 0 10px;
}

.line {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 0 auto 60px;
  background-color: rgba(255, 255, 255, 0.5);
}

.line_bottom {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 120px auto 30px;
  background-color: rgba(255, 255, 255, 0.5);
}

.services_icons_box {
  width: 100%;
  max-width: 1296px;

  margin: 60px auto 0;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;

  grid-template-columns: repeat(auto-fit, minmax(416px, auto));
}

.icon {
  width: 100%;
  min-width: 416px;

  position: relative;
}

.icon_date__box {
  display: flex;
  flex-direction: column;
  justify-self: center;

  position: absolute;
  z-index: 9999;
  margin-left: 35px;
  margin-top: 30px;

  width: 96px;
  height: 30px;
  padding: 6px, 12px, 5px, 12px;
  border-radius: 5px;

  background-color: rgba(1, 138, 190, 1);
}

.icon_date__text {
  display: block;
  width: 100%;
  margin: auto;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
}

@media (max-width: 1318px) {
  .icon_date__box {
    margin-left: 20px;
  }
}

@media (max-width: 1024px) {
  .services_icons_box {
    margin: 50px auto 0;
  }
}

@media (max-width: 905px) {
  .services_icons_box {
    display: block;
    width: 90%;
  }
}

@media (max-width: 730px) {
  .services_icons_box {
    margin: 25px auto 0;
  }

  .line {
    margin: 0 auto 25px;
  }

  .line_bottom {
    margin: 60px auto 30px;
  }

  .icon_date__box {
    margin-left: 10px;
  }

  .icon_date__text {
    font-size: 12px;
  }
}

@media (max-width: 570px) {
  .services_icons_box {
    margin: 0 auto;
  }

  .line {
    margin: 0 auto 10px;
  }

  .icon {
    width: 100%;
    min-width: 0;
  }
}
