.slick-dots li button:before {
  width: 16px;
  height: 16px;

  border-radius: 3px;
  opacity: 40%;
  background: rgba(255, 255, 255, 1);
  font-size: 0;
}

.slick-dots li.slick-active button:before {
  opacity: 0.9;
}

.slick-dots li {
  margin: 0 10px;
}

@media (max-width: 900px) {
  .slick-dots li button:before {
    width: 12px;
    height: 12px;
  }

  .slick-dots li {
    margin: 0 6px;
  }
}

@media (max-width: 570px) {
  .slick-dots li button:before {
    width: 10px;
    height: 10px;
  }

  .slick-dots li {
    margin: 0 5px;
  }
}
