.title_text {
  font-size: 42px;
  font-weight: 700;
  line-height: 59px;
  text-align: center;

  position: relative;
}

.title_text::before {
  content: "";
  width: 28px;
  height: 14px;

  background-image: url(./Rectangle_L_B.png);
  position: absolute;

  top: 35%;
  margin-left: -73px;
}

.title_text::after {
  content: "";
  width: 28px;
  height: 14px;

  background-image: url(./Rectangle_R_B.png);
  position: absolute;

  top: 35%;
  margin-left: 45px;
}

@media (max-width: 730px) {
  .title_text {
    font-size: 28px;
  }
}

@media (max-width: 570px) {
  .title_text {
    font-size: 22px;
  }

  .title_text::before {
    width: 0;
    height: 0;
  }

  .title_text::after {
    width: 0;
    height: 0;
  }
}
