.section__box {
  padding: 0 10px;
}

.items__box {
  display: flex;
  justify-content: space-between;

  width: 100%;
  max-width: 1076px;

  margin: 100px auto;
}

.item__list_box {
  width: 100%;
  max-width: 416px;
}

.item__list {
  display: flex;
  flex-direction: column;
  justify-content: center;

  height: 100%;

  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
  list-style: none;

  font-family: "Roboto Condensed", sans-serif;
}

.item__list_li {
  display: flex;
  justify-content: space-between;
  padding-left: 38px;
  margin-bottom: 31px;
  position: relative;
}

.item__list_li::before {
  content: "";
  width: 18px;
  height: 9px;

  background-image: url(./Rectangle_27.png);
  position: absolute;

  top: 31%;
  margin-left: -37px;
}

.item__li_price {
  font-family: "El Messiri", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  margin-left: 10px;
}

.item__img_box {
  width: 100%;
  max-width: 526px;
  border-radius: 10px;
  margin-left: 20px;
}

.item__img {
  width: 100%;
  max-width: 526px;
  border-radius: 10px;
}

.item__img_text {
  margin-top: 30px;
  font-size: 21px;
  font-weight: 700;
  line-height: 29px;
  text-align: center;
}

@media (max-width: 1024px) {
  .items__box {
    margin: 50px auto;
  }
}

@media (max-width: 980px) {
  .items__box {
    display: block;
  }

  .item__list_box {
    max-width: 526px;
    margin: 0 auto 20px;
  }

  .item__img_box {
    margin: 0 auto;
  }

  .item__img_text {
    margin-top: 20px;
  }
}

@media (max-width: 730px) {
  .items__box {
    margin: 25px auto;
  }

  .item__img_text {
    margin-bottom: 45px;
  }
}

@media (max-width: 570px) {
  .items__box {
    margin: 0 auto;
  }

  .item__li_titleName,
  .item__li_price {
    font-size: 13px;
    margin-bottom: -15px;
  }

  .item__img_text {
    margin-top: 10px;
    font-size: 18px;
  }
}
