.section__box {
  padding: 0 10px;
}

.map_zone__box {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1296px;

  margin: 0 auto 90px;
}

.map__box {
  width: 100%;
  max-width: 856px;
  height: 604px;
  margin-right: 25px;

  border-radius: 10px;
}

.map {
  width: 100%;
  height: 100%;

  border-radius: 10px;
}

.session__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 392px;
}

@media (max-width: 1024px) {
  .map_zone__box {
    margin-bottom: 50px;
  }
}

@media (max-width: 900px) {
  .map_zone__box {
    display: block;
  }

  .map__box {
    margin: 0 auto 20px;
  }

  .session__box {
    flex-direction: row;
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
  }
}

@media (max-width: 570px) {
  .session__box {
    display: block;
    margin: 0 auto;

    width: 100%;
    max-width: 570px;
  }

  .map_zone__box {
    margin-bottom: 25px;
  }
}
