.section__box {
  padding: 0 10px;
}

.items__box {
  width: 100%;
  max-width: 1076px;

  margin: 100px auto 0;
  padding-bottom: 50px;
}

.item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
  font-family: "Roboto Condensed", sans-serif;
}

.item__text_box {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  max-width: 466px;

  margin-top: 17px;
  margin-bottom: 17px;
}

.item__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
  margin-bottom: 30px;
}

.item__text {
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
}

.item__img_box {
  max-width: 526px;
  border-radius: 10px;
}

.item__img {
  width: 526px;
  height: 100%;
  border-radius: 10px;
}

@media (max-width: 1111px) {
  .items__box {
    margin: 50px auto 0;
    padding-bottom: 0;
  }

  .item {
    margin-bottom: 70px;
  }

  .item__text_box {
    margin-left: 10px;
    margin-right: 10px;
  }

  .item__img_box {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media (max-width: 1024px) {
  .items__box {
    margin: 50px auto 0;
    padding-bottom: 20px;
  }

  .item {
    display: block;
    width: 90%;
    margin: 0 auto 50px;
  }

  .item__text_box {
    max-width: 90%;
    margin: 0 auto 25px;
  }

  .item__img_box {
    margin-left: auto;
    margin-right: auto;
  }

  .item__img_box {
    width: 100%;
    max-width: 526px;
    margin-bottom: 30px;
  }

  .item__img {
    width: 100%;
  }
}

@media (max-width: 730px) {
  .item {
    margin-bottom: 35px;
  }

  .items__box {
    margin-top: 25px;
    padding-bottom: 10px;
  }

  .item__title {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .item__text {
    font-size: 14px;
  }
}

@media (max-width: 570px) {
  .items__box {
    margin-top: 0;
  }
  .item {
    margin-bottom: 20px;
  }

  .item__title {
    font-size: 16px;
  }

  .item__text {
    font-size: 13px;
  }
}
