.section__box {
  padding: 0 10px;
}

.line {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 0 auto 60px;
  background-color: rgba(255, 255, 255, 0.5);
}

.line_bottom {
  width: 100%;
  max-width: 1296px;
  height: 1px;
  margin: 120px auto 30px;
  background-color: rgba(255, 255, 255, 0.5);
}

.sessions_box {
  display: flex;
  justify-content: space-between;

  width: 100%;
  max-width: 1296px;
  margin: 60px auto 0;
}

.sessions_text_box {
  width: 100%;
  max-width: 526px;
}

.sessions_text_date {
  display: flex;
  flex-direction: column;
  justify-self: center;

  width: 96px;
  height: 30px;
  margin-bottom: 45px;
  padding: 6px, 12px, 5px, 12px;
  border-radius: 5px;

  background-color: rgba(1, 138, 190, 1);
}

.sessions_text_date_span {
  display: block;
  width: 100%;
  margin: auto;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
}

.sessions_text_title {
  margin-bottom: 30px;

  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.sessions_text {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
}

.sessions_img_box {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  max-width: 636px;

  margin-left: 10px;

  border-radius: 10px;
}

.sessions_img {
  width: 100%;
  max-width: 636px;

  border-radius: 10px;
}

@media (max-width: 1024px) {
  .sessions_box {
    display: block;
  }

  .sessions_text_box {
    margin: 0 auto;
  }

  .sessions_img_box {
    display: blok;

    margin: 30px auto 0;
  }

  .line_bottom {
    margin: 60px auto 30px;
  }
}

@media (max-width: 730px) {
  .line {
    margin: 0 auto 25px;
  }

  .sessions_box {
    margin: 40px auto 0;
  }

  .sessions_text_date {
    margin-bottom: 35px;
  }

  .sessions_text_title {
    font-size: 18px;
    margin-bottom: 25px;
  }

  .sessions_text {
    font-size: 14px;
  }
}

@media (max-width: 570px) {
  .line {
    margin: 0 auto 10px;
  }

  .sessions_text_date {
    margin-bottom: 25px;
  }
  .sessions_text_title {
    font-size: 16px;
    margin-bottom: 15px;
    text-align: center;
  }

  .sessions_text {
    font-size: 12px;
    text-align: center;
  }
}
