.services_icon__link {
  display: block;
  grid-row: 1 / 3;
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
}

.services_icon {
  display: flex;
  flex-direction: column;
  justify-content: end;

  position: relative;

  min-width: 416px;
  height: 540px;

  margin: auto;
  padding: 0 40px 40px 30px;

  border-radius: 10px;

  background-image: url(./unsplashB.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.services_icon:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  min-width: 416px;
  height: 7px;

  background-color: rgba(1, 138, 190, 1);

  bottom: 0;
  left: 0;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.services_icon__text {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

@media (max-width: 1318px) {
  .services_icon {
    padding: 0 30px 30px 20px;
  }

  .services_icon__text {
    font-size: 22px;
  }
}

@media (max-width: 905px) {
  .services_icon {
    height: 258px;
    margin-bottom: 20px;
  }
}

@media (max-width: 730px) {
  .services_icon {
    height: 200px;
  }

  .services_icon {
    padding: 0 20px 20px 10px;
  }

  .services_icon__text {
    font-size: 18px;
  }
}

@media (max-width: 570px) {
  .services_icon {
    min-width: 100px;
  }

  .services_icon__text {
    font-size: 16px;
  }

  .services_icon:hover::after {
    min-width: 100px;
  }
}
