.slick__box {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 150px;

  font-family: "Roboto Condensed", sans-serif;
  text-align: center;
}

.item {
  width: 100%;
  max-width: 500px;
  max-height: 276px;
  min-height: 276px;

  margin-top: 37px;
  padding: 40px 45px 71px;

  border-radius: 10px;

  background-color: rgba(1, 138, 190, 1);
  opacity: 40%;

  text-align: left;
  overflow: hidden;
}

.item_active {
  max-width: 636px;
  max-height: 350px;
  min-height: 350px;

  padding: 35px 45px 50px;
  margin-bottom: 60px;

  border-radius: 10px;
  background-color: rgba(1, 138, 190, 1);
  text-align: left;
}

.item_title {
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;

  margin-bottom: 15px;
}

.item_title_active {
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;

  margin-bottom: 10px;
}

.item_text {
  font-size: 10px;
  font-weight: 300;
  line-height: 14px;
}

.item_text_active {
  font-size: 14px;
  font-weight: 300;
  line-height: 19.6px;
}

@media (max-width: 1550px) {
  .item {
    max-width: 450px;
    padding: 30px 35px 61px;
  }

  .item_active {
    max-width: 586px;
    max-height: 900px;
    min-height: 350px;

    padding: 25px 35px 40px;
    margin-bottom: 50px;
  }
}

@media (max-width: 1440px) {
  .slick__box {
    margin-top: 90px;
  }

  .item {
    max-width: 400px;
    padding: 20px 25px 51px;
  }

  .item_active {
    max-width: 586px;
    padding: 15px 25px 30px;
    margin-bottom: 45px;
  }
}

@media (max-width: 1320px) {
  .slick__box {
    margin-top: 80px;
  }

  .item {
    max-width: 350px;
    padding: 15px 20px 40px;
  }

  .item_active {
    max-width: 586px;
    padding: 10px 20px 25px;
    margin-bottom: 40px;
  }

  .item_title_active {
    margin-bottom: 5px;
  }
}

@media (max-width: 1290px) {
  .slick__box {
    margin-top: 75px;
  }

  .item {
    max-width: 200px;
    padding: 15px 20px 40px;
  }

  .item_active {
    padding: 10px 20px 25px;
    margin-bottom: 35px;
  }
}

@media (max-width: 1024px) {
  .slick__box {
    margin-bottom: 100px;
  }
}

@media (max-width: 900px) {
  .slick__box {
    margin-top: 70px;
  }

  .item_active {
    max-width: 520px;
    padding: 10px 20px 10px;
    margin-bottom: 25px;
  }
}

@media (max-width: 730px) {
  .slick__box {
    margin-top: 50px;
    margin-bottom: 60px;
  }

  .item_active {
    margin-bottom: 15px;
  }

  .item_title_active {
    font-size: 18px;
  }

  .item_text_active {
    font-size: 13px;
  }
}

@media (max-width: 570px) {
  .slick__box {
    margin-top: 25px;
    margin-bottom: 40px;
  }

  .item_active {
    max-width: 420px;
    margin-bottom: 10px;
  }

  .item_title_active {
    font-size: 16px;
  }

  .item_text_active {
    font-size: 12px;
  }
}
