.section__box {
  padding: 0 10px;
}

.main_rs__box {
  width: 100%;
  max-width: 1296px;
  height: 600px;

  margin: 20px auto 150px;
  padding: 136px 0 210px;

  border-radius: 10px;

  background-image: url(./unsplash.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.main_rs__sub_tittle {
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  padding-bottom: 40px;
  position: relative;
}

.main_rs__sub_tittle::before {
  content: "";
  width: 28px;
  height: 14px;

  background-image: url(./Rectangle_L.png);
  position: absolute;

  margin-left: -90px;
}

.main_rs__sub_tittle::after {
  content: "";
  width: 28px;
  height: 14px;

  background-image: url(./Rectangle_R.png);
  position: absolute;

  margin-left: 66px;
}

.main_rs__title {
  width: 100%;
  max-width: 587px;

  margin: 0 auto;

  font-size: 64px;
  font-weight: 700;
  text-align: center;
  line-height: 89.6px;
}

@media (max-width: 1024px) {
  .main_rs__box {
    margin-bottom: 100px;
  }
}

@media (max-width: 730px) {
  .main_rs__box {
    height: auto;
    margin: 0 auto 60px;
    padding: 65px 0 150px;
  }
  .main_rs__title {
    font-size: 50px;
    line-height: 0;
  }
  .main_rs__sub_tittle {
    padding-bottom: 70px;
  }
}

@media (max-width: 630px) {
  .main_rs__sub_tittle::before {
    margin-left: -70px;
  }

  .main_rs__sub_tittle::after {
    margin-left: 46px;
  }
}

@media (max-width: 570px) {
  .main_rs__box {
    padding: 50px 0 60px;
    margin: 0 auto 25px;
  }

  .main_rs__sub_tittle {
    font-size: 18px;
    padding-bottom: 45px;
  }

  .main_rs__sub_tittle::before {
    width: 0;
    height: 0;
  }

  .main_rs__sub_tittle::after {
    width: 0;
    height: 0;
  }

  .main_rs__title {
    font-size: 26px;
  }
}
