.session__item {
  width: 100%;
  max-width: 392px;

  padding: 35px 45px;

  border-radius: 10px;

  background: rgba(1, 138, 190, 1);
}

.session__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
  padding-bottom: 20px;
}

.session__li {
  list-style: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  padding-left: 40px;
  padding-bottom: 21px;
  font-family: "Roboto Condensed", sans-serif;

  position: relative;
}

.session__li_1::before {
  content: "";

  width: 20px;
  height: 20px;

  background-image: url(./1.png);
  position: absolute;

  left: -2px;
}

.session__li_2::before {
  content: "";

  width: 20px;
  height: 16px;

  background-image: url(./2.png);
  position: absolute;

  left: 0;
}

.session__li_3::before {
  content: "";

  width: 20px;
  height: 20px;

  background-image: url(./3.png);
  position: absolute;

  left: 0;
}

.session__btn {
  width: 100%;
  max-width: 155px;
  height: 36px;

  margin-top: 9px;
  padding: 9px, 15px, 9px, 15px;
  border: none;
  border-radius: 3px;

  font-family: "Roboto Condensed", sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 18px;

  color: rgba(1, 138, 190, 1);
  cursor: pointer;
}

.session__btn:hover {
  color: #fff;
  box-shadow: inset 0 0 45px #00c8ff;
}

.session__btn:active {
  box-shadow: inset 0 0 45px rgba(1, 138, 190, 1);
}

@media (max-width: 900px) {
  .session__item {
    width: 100%;
    max-width: 424px;
    margin: 0 5px;
  }
}

@media (max-width: 730px) {
  .session__item {
    padding: 15px 15px 20px;
  }

  .session__title {
    font-size: 18px;
    padding-bottom: 12px;
  }

  .session__li {
    font-size: 14px;
    padding-bottom: 10px;
  }

  .session__btn {
    display: block;
    margin: 20px auto 0;
  }
}

@media (max-width: 570px) {
  .session__item {
    max-width: 440px;
    margin: 0 auto 20px;
  }
}
