.box_nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1298px;
  min-height: 60px;
  margin: 0 auto;
  padding: 30px 10px;
}

.logo_nav {
  min-width: 60px;
  min-height: 60px;
}

.ul_nav {
  display: flex;
  justify-content: space-between;

  margin: 25px auto;

  list-style: none;
  font-size: 14px;
  font-weight: 400;

  font-family: "Roboto Condensed", sans-serif;
}

.li_a__nav {
  margin: 0 25px 0;
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
}

.li_a__nav:hover {
  color: #00c8ff;
}

.button_nav {
  display: block;

  width: 155px;
  height: 36px;

  margin-top: 14px;

  border-radius: 3px;

  font-size: 14px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  background: rgba(1, 138, 190, 1);

  border: none;
  font-family: "Roboto Condensed", sans-serif;
  cursor: pointer;
}

.button_nav:hover {
  box-shadow: inset 0 0 45px #00c8ff;
}

.button_nav:active {
  box-shadow: inset 0 0 45px rgba(1, 138, 190, 1);
}

@media (max-width: 935px) {
  .li_a__nav {
    margin: 0 15px 0;
  }
}

@media (max-width: 935px) {
  .li_a__nav {
    margin: 0 15px 0;
  }
}

@media (max-width: 800px) {
  .li_a__nav {
    margin: 0 10px 0;
  }
}

@media (max-width: 730px) {
  .box_nav {
    display: block;
  }

  .logo_nav {
    display: block;
    margin: 0 auto;
  }

  .button_nav {
    width: 80%;
    height: 36px;

    margin: 0 auto;
  }
}

@media (max-width: 510px) {
  .ul_nav {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .li_nav {
    margin-bottom: 10px;
  }
}
