.section__box {
  padding: 0 10px;
}

.services_icons_box {
  width: 100%;
  max-width: 1296px;

  margin: 100px auto 150px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;

  grid-template-columns: repeat(auto-fit, minmax(416px, auto));
}

@media (max-width: 1024px) {
  .services_icons_box {
    margin: 50px auto 100px;
  }
}

@media (max-width: 905px) {
  .services_icons_box {
    display: block;
    width: 90%;
  }
}

@media (max-width: 730px) {
  .services_icons_box {
    margin: 25px auto 60px;
  }
}

@media (max-width: 570px) {
  .services_icons_box {
    margin: 0 auto 25px;
  }
}
