.footer {
  display: block;
  padding: 0 10px;
}

.footer_box {
  width: 100%;
  max-width: 1298px;
  margin: 0 auto;
  padding-bottom: 30px;
  font-size: 14px;
  font-weight: 400;
  text-align: left;
}

.footer_text {
  display: block;
  width: 150px;
}

@media (max-width: 570px) {
  .footer_box {
    padding-bottom: 30px;
  }
}
