.item_box {
  display: flex;
  justify-content: space-between;

  margin-bottom: 45px;

  width: 100%;

  border-radius: 20px;

  border: 2px solid rgba(1, 138, 190, 1);

  padding: 50px 60px;
}

.item_img_box {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  max-width: 564px;
  border-radius: 10px;

  margin-right: 20px;
}

.item_img {
  width: 100%;
  max-width: 564px;
  border-radius: 10px;
}

.item_text_box {
  width: 100%;
  max-width: 564px;
}

.item_text_date {
  display: flex;
  flex-direction: column;
  justify-self: center;

  width: 96px;
  height: 30px;
  margin-bottom: 30px;
  padding: 6px, 12px, 5px, 12px;
  border-radius: 5px;

  background-color: rgba(1, 138, 190, 1);
}

.item_text_date_span {
  display: block;
  width: 100%;
  margin: auto;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
}

.item_text_title {
  margin-bottom: 20px;

  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.item_text {
  margin-bottom: 40px;

  font-family: "Roboto Condensed", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
}

.item_btn {
  display: block;
  width: 100%;
  max-width: 134px;
  height: 39px;

  padding: 10px, 20px, 10px, 20px;

  border: none;
  border-radius: 5px;
  background-color: rgba(1, 138, 190, 1);

  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 19px;
  color: rgba(255, 255, 255, 1);

  cursor: pointer;
}

.item_btn:hover {
  box-shadow: inset 0 0 45px #00c8ff;
}

.item_btn:active {
  box-shadow: inset 0 0 45px rgba(1, 138, 190, 1);
}

@media (max-width: 1270px) {
  .item_box {
    padding: 20px 30px;
  }
}

@media (max-width: 1270px) {
  .item_text_date {
    margin-bottom: 15px;
  }

  .item_text_title {
    margin-bottom: 10px;
  }

  .item_text {
    margin-bottom: 20px;
  }
}

@media (max-width: 1024px) {
  .item_box {
    display: block;
    width: 90%;
    margin: 0 auto 30px;
  }

  .item_img_box {
    margin: 0 auto 20px;
  }

  .item_text_box {
    margin: 0 auto;
  }

  .item_text_title {
    text-align: center;
  }

  .item_text_date {
    margin-bottom: 5px;
  }

  .item_btn {
    margin: 0 auto;
    max-width: 174px;
    margin-bottom: 20px;
  }
}

@media (max-width: 730px) {
  .item_box {
    padding: 15px 25px;
  }

  .item_text_title {
    font-size: 18px;
  }

  .item_text {
    font-size: 14px;
  }

  .item_btn {
    margin-bottom: 10px;
  }
}

@media (max-width: 570px) {
  .item_box {
    padding: 10px 20px;
  }

  .item_text_title {
    font-size: 16px;
  }

  .item_text {
    font-size: 12px;
    text-align: center;
  }

  .item_btn {
    margin-bottom: 5px;
  }
}
